label {
  display: block;
}
.App {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-content: space-between;
}

.Header {
  color: aqua;
  text-align: center;
  text-decoration: aqua underline;
}

.Search {
  margin: 50px auto;
}

li {
  list-style: none;
}

label {
  width: 100px;
  margin: 10px;
  color: blueviolet;
  display: inline;
}

input{
  display: inline;
  margin: 10px;
  padding: 3px;
  outline: cornflowerblue outset 2px;
  width: 200px;
  height: 60px;
}